<script>
import CartContent from '../../components/CartContent.vue';

export default {
  components: { CartContent },
  computed: {
    orders() {
      let orders = [];
      this.$bus.$on('son_get_data', (data) => {
        orders = data.orders;
      });
      this.$bus.$emit('son_call_data');
      return orders;
    },
    orderId() {
      return this.$route.params.orderId;
    },
    thisOrder() {
      return this.orders.find((item) => item.id === this.orderId);
    },
  },
  data() {
    return {
      orderInfoCollapse: true,
      paymentInfoCollapse: true,
      payment: {},
    };
  },
  methods: {
    convert_time(createTime) {
      const theDate = new Date(createTime);
      const year = theDate.getFullYear();
      const month = theDate.getMonth() + 1;
      const day = theDate.getDate();
      const hour = theDate.getHours();
      const minute = theDate.getMinutes();
      const second = theDate.getSeconds();
      const twoDigits = (num) => (num > 9 ? '' : '0') + num;
      const timeStr = `${year}-${twoDigits(month)}-${twoDigits(day)} ${twoDigits(hour)}:${twoDigits(minute)}:${twoDigits(second)}`;
      return timeStr;
    },
    convert_text(value) {
      let text = '';
      switch (value) {
        case 'person':
          text = '個人電子發票';
          break;
        case 'donation':
          text = '捐贈電子發票';
          break;
        case 'company':
          text = '公司報帳電子發票';
          break;
        case 'no':
          text = '不使用載具';
          break;
        case 'mobile':
          text = '手機條碼載具';
          break;
        case 'nature':
          text = '自然人憑證載具';
          break;
        case 'donationComapny1':
          text = '捐贈單位1';
          break;
        case 'donationCompany2':
          text = '捐贈單位2';
          break;
        case 'donationCompany3':
          text = '捐贈單位3';
          break;
        case true:
          text = '是';
          break;
        case false:
          text = '否';
          break;
        case '':
          text = '不指定';
          break;
        case 'morning':
          text = '上午( 08:00 ~ 13:00 )';
          break;
        case 'afternoon':
          text = '下午( 14:00 ~ 18:00 )';
          break;
        default:
          break;
      }
      return text;
    },
    get_paymentInfoType(value) {
      const newObj = {};
      switch (value) {
        case 'creditCard':
          newObj.type = value;
          newObj.creditNumber = '';
          newObj.creditExp = {
            month: '',
            year: '',
          };
          newObj.creditCvc = '';
          break;
        default:
          break;
      }
      this.$set(this, 'payment', newObj);
    },
    pay_order() {
      this.payment.time = this.convert_time(new Date());
      this.$bus.$emit('make_payment', this.thisOrder.id, this.payment);
    },
  },
  created() {
    const createPaymentInfo = () => {
      const ifPaidBoolean = this.thisOrder.payment.ifPaid;
      const newPaymentInfoObj = {
        type: '',
      };
      if (!ifPaidBoolean) {
        this.$set(this.thisOrder.payment, 'info', newPaymentInfoObj);
      }
    };
    createPaymentInfo();
    // 暫定付款方式只有信用卡
    this.get_paymentInfoType('creditCard');
  },
};
</script>
